import * as React from 'react';
import { classNames } from '@vkontakte/vkjs';
import type { HasRef, HasRootRef } from '../../types';
import { type ImageBaseBadgeProps, ImageBaseBadge } from './ImageBaseBadge/ImageBaseBadge';
import { type ImageBaseOverlayProps, ImageBaseOverlay } from './ImageBaseOverlay/ImageBaseOverlay';
import type { ImageBaseExpectedIconProps, ImageBaseSize, ImageBaseContextProps } from './types';
import { ImageBaseContext } from './context';
import { validateSize, validateFallbackIcon } from './validators';
import styles from './ImageBase.module.css';

export type {
  ImageBaseSize,
  ImageBaseExpectedIconProps,
  ImageBaseBadgeProps,
  ImageBaseOverlayProps,
  ImageBaseContextProps,
};

export {
  getBadgeIconSizeByImageBaseSize,
  getFallbackIconSizeByImageBaseSize,
  getOverlayIconSizeByImageBaseSize,
} from './helpers';

export { ImageBaseContext };

export interface ImageBaseProps
  extends React.ImgHTMLAttributes<HTMLElement>,
    HasRootRef<HTMLDivElement>,
    HasRef<HTMLImageElement> {
  /**
   *   .
   *
   *    - `16 | 20 | 24 | 28 | 32 | 36 | 40 | 44 | 48 | 56 | 64 | 72 | 80 | 88 | 96`.
   *
   * >        .
   */
  size?: ImageBaseSize | number;
  /**
   *    .
   */
  withBorder?: boolean;
  /**
   *   ,    .
   *
   * >    `<ImageBase size={...} />`      `getFallbackIconSizeByImageBaseSize()`.
   *
   * >     `@vkontakte/icons`.
   *
   * >       ,       `Icon<size><name>`.  
   * >       `width`.           
   * >  .
   *
   * >    `children`.
   */
  fallbackIcon?: React.ReactElement<ImageBaseExpectedIconProps>;
}

/**
 * @see https://vkcom.github.io/VKUI/#/ImageBase
 */
export const ImageBase = ({
  alt,
  crossOrigin,
  decoding,
  loading,
  referrerPolicy,
  sizes,
  src,
  srcSet,
  useMap,
  getRef,
  size = 24,
  width,
  height,
  style,
  className,
  getRootRef,
  withBorder = true,
  'fallbackIcon': fallbackIconProp,
  children,
  'aria-label': ariaLabel,
  onClick,
  onLoad,
  onError,
  ...restProps
}: ImageBaseProps) => {
  const [loaded, setLoaded] = React.useState(false);
  const [failed, setFailed] = React.useState(false);

  const hasSrc = src || srcSet;
  const needShowFallbackIcon = (failed || !hasSrc) && React.isValidElement(fallbackIconProp);

  const fallbackIcon = needShowFallbackIcon ? fallbackIconProp : null;

  if (process.env.NODE_ENV === 'development') {
    validateSize(size);
    if (fallbackIcon) {
      validateFallbackIcon(size, { name: 'fallbackIcon', value: fallbackIcon });
    }
  }

  const handleImageLoad = (event: React.SyntheticEvent<HTMLImageElement>) => {
    setLoaded(true);
    setFailed(false);
    onLoad?.(event);
  };

  const handleImageError = (event: React.SyntheticEvent<HTMLImageElement>) => {
    setLoaded(false);
    setFailed(true);
    onError?.(event);
  };

  return (
    <ImageBaseContext.Provider value={{ size }}>
      <div
        {...restProps}
        ref={getRootRef}
        style={{ ...style, width: size, height: size }}
        className={classNames(
          className,
          styles['ImageBase'],
          styles[`ImageBase--size-${size as ImageBaseSize}`],
          withBorder && styles['ImageBase--withBorder'],
          loaded && styles['ImageBase--loaded'],
        )}
        role={hasSrc ? 'img' : 'presentation'}
        aria-label={ariaLabel}
        onClick={onClick}
      >
        {hasSrc && (
          <img
            ref={getRef}
            alt={alt}
            className={styles['ImageBase__img']}
            crossOrigin={crossOrigin}
            decoding={decoding}
            loading={loading}
            referrerPolicy={referrerPolicy}
            sizes={sizes}
            src={src}
            srcSet={srcSet}
            useMap={useMap}
            width={width}
            height={height}
            onLoad={handleImageLoad}
            onError={handleImageError}
          />
        )}
        {fallbackIcon && (
          <div className={classNames(styles['ImageBase__fallback'])}>{fallbackIcon}</div>
        )}
        {children}
      </div>
    </ImageBaseContext.Provider>
  );
};

ImageBase.Badge = ImageBaseBadge;

ImageBase.Overlay = ImageBaseOverlay;
