import * as React from 'react';
import { classNames } from '@vkontakte/vkjs';
import type { HasRootRef, AlignType } from '../../types';
import styles from './ButtonGroup.module.css';

export interface ButtonGroupProps
  extends React.HTMLAttributes<HTMLDivElement>,
    HasRootRef<HTMLDivElement> {
  /**
   *     ,   .
   */
  mode?: 'vertical' | 'horizontal';
  /**
   *     `mode`     .
   */
  gap?: 'none' | 'space' | 's' | 'm';
  /**
   *      .
   *
   * Note:       ,   .
   */
  stretched?: boolean;
  /**
   *     .    mode="vertical".
   */
  align?: AlignType;
}

/**
 * @see https://vkcom.github.io/VKUI/#/ButtonGroup
 */
export const ButtonGroup = ({
  mode = 'horizontal',
  gap = 'm',
  stretched = false,
  align = 'left' /* NOTE:   -       */,
  getRootRef,
  className,
  children,
  ...restProps
}: ButtonGroupProps) => {
  return (
    <div
      className={classNames(
        className,
        styles.ButtonGroup,
        styles[`ButtonGroup--mode-${mode}`],
        gap !== 'none' && styles[`ButtonGroup--gap-${gap}`],
        stretched && styles['ButtonGroup--stretched'],
        align && styles[`ButtonGroup--align-${align}`],
      )}
      role="group"
      ref={getRootRef}
      {...restProps}
    >
      {children}
    </div>
  );
};
